@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-image:url(/aprozar-la-garaj/static/media/home.657bfcef.jpeg);background-position:center;font-family:'Comfortaa', cursive;color:white}.App{height:100vh;overflow-y:scroll}.main{background-image:linear-gradient(to right, #2e2d2d, #35352d);min-height:100vh;padding:6vh 2em 0vh 2em;color:white}*{box-sizing:border-box}.center{text-align:center}

.navigation{position:fixed;top:0;width:100%;z-index:9}.nav-bg{background-image:linear-gradient(to right, #2e2d2d, #35352d)}#menu{transition:.2s}.link{cursor:pointer}.link:focus{outline:none}.link:active{outline:none}.left-100{left:100%}.left-0{left:0}

.particles{position:fixed;top:0;bottom:0;right:0;left:0;z-index:-1}header{height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;text-align:center;line-height:7vh}header h1{font-size:10vw;font-family:'Pacifico', cursive;color:white;text-shadow:1px 1px 1px rgba(0,0,0,0.4)}header h2{font-family:'Comfortaa', cursive;font-feature-settings:normal;font-variant-caps:normal;font-size:3vw;padding:1em;background:linear-gradient(to right, #2e2d2d, #35352d);color:yellow;z-index:-1}

h1{font-size:1em}h2{font-size:.8em}.grow:hover{z-index:2;transition:.3s}.grow{z-index:0;transition:.3s}

.bradius{border-radius:25px}.w75{width:30vw}


.group{position:relative;margin:45px 0}.group .form-input{background:none;background-color:white;color:#353535;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid #353535;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-18px;font-size:14px;color:#fff}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:#353535;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-18px;font-size:14px;color:#fff}

.custom-button{min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-family:'Open Sans Condensed';font-weight:bolder;border:none;cursor:pointer}.custom-button:hover{background-color:white;color:black;border:1px solid black}.custom-button.google-sign-in{background-color:#4285f4;color:white}.custom-button.google-sign-in:hover{background-color:#357ae8;border:none}

.sign-in{display:flex;flex-direction:column;padding:0 1em}.buttons{display:flex;justify-content:space-between}

.sign-up{display:flex;flex-direction:column;padding:0 1em}.sign-up .title{margin:10px 0}


